import DoughnutChart from '../charts/DoughnutChart'
import LineChart from '../charts/LineChart';
import PolarAreaChart from '../charts/PolarAreaChart';
import { StyledInformation } from '../styled/Information.styled';

const Information = () => {
    return (
        <StyledInformation>
            <section>
                <h2>Notcias por ms</h2>
                <LineChart />
            </section>
            <section>
                <h2>Top 3 autores</h2>
                <PolarAreaChart />
            </section>
            <section>
                <h2>Notcias publicadas por autor</h2>
                <sup>Passe o mouse sobre as sees</sup>
                <DoughnutChart />
                {/* legend display off because takes
                too much of space */}
            </section>
        </StyledInformation>
     );
}
 
export default Information;
